<template>
  <div>
    <div class="stepper-header--wrapper">

      <div class="stepper-header--item">
        <div class="stepper--counter">1</div><label v-bind:class="{ 'active': this.step == 0 }">name of step </label>
      </div>

      <div class="stepper--divider"></div>

      <div class="stepper-header--item">
        <div class="stepper--counter">2</div><label v-bind:class="{ 'active': this.step == 1 }" >name of step </label>
      </div>

      <div class="stepper--divider"></div>

      <div class="stepper-header--item">
        <div class="stepper--counter">3</div><label v-bind:class="{ 'active': this.step == 2 }" >name of step </label>
      </div>

    </div>
    <div class="stepper-content--wrapper">
      <div class="stepper-content--item" v-if="this.step == 0">
        Content 1
      </div>
      <div class="stepper-content--item" v-if="this.step == 1">
        Content 2
      </div>
      <div class="stepper-content--item" v-if="this.step == 2">
        Content 3
      </div>
    </div>
    <div class="stepper-controls--wrapper">
      <button class="btn--next" @click="this.next"> Continue </button> <button class="btn--back" @click="this.back"> Back </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Stepper',
  data () {
    return {
      step: 0
    }
  },
  methods: {
    next () {
      if (this.step < 2) {
        this.step++;
      }
      console.log(this.step)
    },
    back () {
      if (this.step > 0) {
        this.step--;
      }
      console.log(this.step)
    }
  }
}
</script>
